  padding: 4rem 0 4rem 0;
  color: $purple;
  a {
    color: $purple;
    &:hover {
      color: $red; } }

  h1 {
    @include fontmake($family: "Montserrat", $size: 1.6, $weight: 400);
    text-align: center; } }

