// footer
.footer-main {
  position: relative;
  padding-bottom: 14rem;
  &::after {
    content: '';
    display: block;
    width: 30px;
    height: 31px;
    @include pos($left: 50%, $bottom: 6rem);
    margin-left: -10px;
    background-color: $bg;
    background-image: url('../images/ftr_mark.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px 21px;
    padding: 5px;
    border-radius: 50%; } }

// issue-image
.issue-rand {
  position: relative;
  text-align: center;
  .issue-link {
    position: absolute;
    z-index: 100;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden; }
  .issue-image {
    img {
      max-width: 100%; } }
  .overlay {
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;
    bottom: 8px;
    left: 0;
    overflow: hidden;
    background-color: rgba(0,0,0,0);
    @extend .transition; }
  .issue-meta {
    position: absolute;
    top: 50%;
    width: 100%;
    opacity: 0;
    @extend .transition;
    p {
      @include fontmake($family: 'Alegreya', $size: 2, $margin: 0);
      color: white;
      text-align: center;
      padding: 0 2rem; } }
  &:hover {
    .overlay {
      background-color: rgba(0,0,0,0.6); }
    .issue-meta {
      opacity: 1; } } }

// tagline
.tagline {
  position: relative;
  margin-bottom: 4rem;
  h1 {
    @include fontmake($family: "Aleo", $size: 2, $margin: 0, $line: 2.4, $weight: 700);
    color: $base;
    text-align: left;
    text-transform: none; } }

// newsletter
.newsletter {
  margin-bottom: 4rem;
  #mc_embed_signup_scroll {
    position: relative;

    .mc-field-group {
      position: relative;
      max-width: 500px; }

    label {
      @include fontmake($family: "Raleway", $size: 1.8, $weight: 400); }

    input#mce-EMAIL {
      @extend .button;
      max-width: 500px;
      width: 100%; }

    #mc-embedded-subscribe {
      @extend .button-submit; }

    .mce_inline_error {
      background-color: transparent;
      color: $red; } } }


.footer-main nav {
  position: relative;
  margin-bottom: 4rem;
  a {
    @include fontmake($size: 1, $margin: 0, $family: "Raleway", $weight: 400);
    margin-right: 1rem;
    &:hover {
      text-decoration: none;
      @include textback($pull: 6px, $sides: 0px, $color: #ECDBBC); } } }
