.banner {
  position: fixed;
  width: 100%;
  z-index: 100; }

.nav-primary {
  position: absolute;
  top: 43px;
  left: 1.7857142857%;
  &::after {
    content: '';
    display: block;
    width: 30px;
    height: 31px;
    position: absolute;
    top: 8px;
    left: 10px;
    margin-left: -10px;
    background-color: $bg;
    background-image: url('../images/ftr_mark.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px 21px;
    padding: 5px;
    border-radius: 50%; }

  .nav-bunch {
    display: inline-block;
    padding: 13px 20px 11px 38px;
    background-color: #fbfaf6;
    border-radius: 25px;
    text-align: left;
    a {
      @include fontmake($size: 1, $margin: 0, $family: "Raleway", $weight: 400);
      margin-right: 1rem;
      &:last-of-type {
        margin-right: 0; }
      &:hover {
        text-decoration: none;
        @include textback($pull: 6px, $sides: 0px, $color: #ECDBBC); } } } }

