.nav-brand {
  // height: 175px
  .ftr-title {
    background-image: url('../images/ftr_title_black.png');
    background-size: cover;
    background-repeat: no-repeat;
    width: 300px;
    height: 29px;
    margin: 0 auto;
    text-indent: 200%;
    white-space: nowrap;
    overflow: hidden;
    transition: 0.3s opacity ease, 0.5s margin ease; }
  .ftr-logo {
    background-image: url('../images/ftr_logo.png');
    background-size: 100px 100px;
    background-repeat: no-repeat;
    width: 100px;
    height: 100px;
    margin: 1rem auto;
    border-radius: 50%;
    position: relative;
    transition: 0.3s all ease-in-out;
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    box-shadow: 0 0 0 1px $red, 0 0 0 11px $yellow;
    &:hover {
      box-shadow: 0 0 0 12px $red; } } }

.nav-brand.nav-scrolled {
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  .ftr-title {
    opacity: 0;
    margin: -180px auto 0 auto; }
  .ftr-logo {
    transform: scale(0.7); } }

.nav-social {
  margin-top: 56px;
  text-align: right;
  a {
    display: inline-block;
    width: 28px;
    height: 28px;
    background-color: #ECDBBC;
    border-radius: 50%;
    text-align: center;
    &:hover {
      text-decoration: none;
      color: $orange; }
    i {
      font-size: 18px;
      position: relative;
      top: 4px; } } }
